.body {
    width: 100%;
    height: 90vh;
    cursor: default;
}

.scroll {
    height: auto;
    overflow-y: scroll;
    scroll-snap-type: y mandatory;
}

.scroll section {
    scroll-snap-align: start;
    position: relative;
}

.sectionIntro {
    width: 100%;
    height: 85vh;
    display: inline-flex;
    flex-direction: row;
    align-items: center;
}

.splash {
    width: 60%;
}

.top_arrow {
    width: 15px;
    height: 15px;
    position: fixed;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(-45deg);
    margin-left: 95%;
    margin-top: 60px;
    cursor: pointer;
}

.intro_description {
    width: 65%;
    margin-right: 5%;
    text-align: left;
    padding-left: 2%;
    border-left: 10px solid #ffc4c4;
}

.intro_subtitle {
    font-size: 6vw;
    font-family: 'Roboto Condensed', sans-serif;
}

.space_divider {
    height: 5vh;
}

.sectionPrototype, .sectionArt {
    width: 80vw;
    margin-left: calc(20vw - 10px);
    height: 100%;
    display: inline-block;
    flex-direction: row;
    padding-left: 10px;
    margin-right: 0;
    cursor: default;
}

.main_title {
    font-size: 250%;
    text-align: left;
    border-bottom: 5px solid #ffc4c4;
}

.sub_title {
    font-size: 30px;
    font-family: 'Lato', sans-serif;
    font-weight: 700;
}

.date, .view_link {
    font-size: 18px;
    font-family: 'Lato', sans-serif;
}

.view_link {
    color: #d97c7c;
}

.display {
    width: 100%;
    min-height: 100vh;
}

.subsection {
    display: inline-flex;
    height: 90vh;
}

.subsection_col {
    position: relative;
    width: 15vw;
    min-width: 210px;
    height: 100vh;
    margin: 0 15% 10% -26%;
    padding: 0px 25px 0px 25px;
    &::after {
        content: '';
        height: 90%; 
        width: 1px;
    
        position: absolute;
        right: 0;
        top: 0; 
    
        border-right: 10px solid transparent;
        border-image: linear-gradient(0deg, #ffffff, #bee7b8);
        border-image-slice: 1;
    }
}

.showcase {
    width: calc(80% - 20px);
    min-width: calc(80% - 20px);
    max-width: 100%;
    max-height: 80vh;
    display: inline-block;
    text-align: left;
    overflow-y: scroll;
    align-content: center;
}

.align_showcase {
    display: flex;
    flex-direction: column;
    align-content: center;
}

.model_showcase {
    width: calc(80% - 20px);
    height: 100vh;
    display: inline-flex;
    justify-content: left;
    flex-wrap: wrap;
    align-content: flex-start;
}

.desktop_sample {
    display: inline-flex;
    text-align: center;
    width: calc(95% - 30px);
    max-width: 70%;
    border: 5px solid lightgrey;
    padding: 5px;
    border-radius: 25px;
    margin-left: 13.5%;
}

.mobile_img {
    display: inline-flex;
    text-align: center;
    width: 30%;
    border: 5px solid rgb(97, 97, 97);
    padding: 5px;
    border-radius: 25px;
    margin-left: 33.5%;
}

.showcase_description {
    width: 70%;
    font-size: 20px;
    display: inline-flex;
    text-align: center;
    margin-left: 15%;
    font-family: 'Lato', sans-serif;
}

.info_col {
    width: 100%;
    display: flex;
    align-items: center;
}

.decor_arrow, .decor_arrow2, .decor_arrow3 {
    width: 15px;
    height: 15px;
    position: absolute;
    border-right: 5px solid #bee7b8;
    border-top: 5px solid #bee7b8;
    transform: rotate(45deg);
    margin-top: 12px;
    margin-left: max(225px, calc(15vw + 15px));
}

.arrow_row {
    width: 15px;
    display: flex;
    position: absolute;
    margin-top: 0px;
}

.arrow_instruct {
    font-size: 18px;
    text-align: left;
    margin-left: 15px;
    bottom: 10px;
}

.arrow {
    width: 15px;
    height: 15px;
    position: relative;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(135deg);
    margin-left: 55px;
    margin-top: -10px;
}

.up_arrow {
    width: 15px;
    height: 15px;
    position: relative;
    border-right: 3px solid black;
    border-top: 3px solid black;
    transform: rotate(-45deg);
    margin-left: 55px;
    cursor: pointer;
}

.up_arrow_row {
    width: 15px;
    margin-top: -40px;
}

.sectionMore {
    width: 80%;
    height: 100vh;
    display: inline-block;
    justify-content: center;
}

.case_study_intro {
    display: inline-flex;
    padding-bottom: 30px;
    border-bottom: 1px solid black;
}

.sum_title {
    font-size: 60px;
    line-height: 1;
}

.proj_sum {
    text-align: left;
}

.sum {
    line-height: 2;
}

.kingpin_sample2 {
    max-width: 50%;
    height: 100%;
    border: 5px solid lightgrey;
    padding: 5px;
    margin-left: 20px;
    border-radius: 25px;
}

.case_study {
    width: 60%;
    height: 100vh;
    display: inline-block;
    justify-content: center;
}

.showcase_img {
    display: inline-block;
    height: 29%;
    margin: 5px;
}

iframe {
    margin: 5px;
}
